.starter {
  font-size: 18px;
}

*{
  margin: 0;
  padding: 0;
}

html,body,#root,.App{
  height: 100%;
}

.App{
  background: linear-gradient(-45deg,rgba(0, 12, 186, 1) 0%,rgba(12, 8, 19, 1) 53%,rgba(0, 12, 186, 1) 100%);
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.board{
  padding: 14px;
  border-radius: 14px;
  background-color: #373855;

  &__wrapper {
    width:calc(64px * 8);
    height:calc(64px * 8);
    display: flex;
    flex-wrap: wrap;
    border-radius: 10px;
    overflow: hidden;
  }
}

.cell{
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cell > img{
  width: 48px;
  height: 48px;
  position: relative;
}

.black{
  background: #0063f7;
}

.white{
  background-color: rgb(127, 233, 127);
}

.selected{
  background-color: brown;
}

.available{
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: #22c022;
}

.green{
  background-color: #22c022;
}

.lost {
  display: flex;
  &__img {
    height: 20px;
    width: 20px;
  }

  & .left-side {
    justify-items: flex-start;
  }
}

.board {
  position: relative;
  
  &__identification-bottom,&__identification-right,
  &__identification-top,&__identification-left {
    position: absolute;
    display: flex;
    flex-direction: row;
    color: white;
    font-family: Inter ,sans-serif;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    justify-content: space-between;
    box-sizing: border-box;

     & > .board__identification-item {
      display: flex;
      justify-content: center;
      align-items: center;
     }
  }

  &__identification-bottom,&__identification-top {
    left: 0;
    // margin: 0 14px;
    height: 14px;
    padding: 0 0.78rem;
    width: 100%;

    & > :nth-child(n) {
      width: 3.558rem;
    }
  }

  &__identification-right,&__identification-left {
    flex-direction: column;
    padding: 0.78rem 0;
    width: 14px;
    height: 100%;
    top: 0;
    
    & > :nth-child(n){
      height: 3.558rem;
    }
  }

  &__identification-top {
    top: 0;
  }

  &__identification-item {
    color:  #E6E6E6;
  }

  &__identification-left { 
    left: 0;
  }

  &__identification-bottom {
    bottom: 0;
  } 

  &__identification-right{
    right: 0;
  }  
}